.header__button {
  width: 40px;
  height: 40px;
  line-height: 0;
  border-radius: 20px;
  background-color: #ff3e79;
  border: 0 dashed white;
  opacity: 1;
  transition: transform 3s ease-in-out, border 1s ease-in-out, opacity 3s; }

.header__button:hover {
  transform: rotate(150deg);
  border: 2px dashed white;
  opacity: 0.5; }

.group {
  display: flex;
  justify-content: flex-start;
  flex-grow: 1; }

.left-float-menu__main {
  flex-grow: 1; }

.left-float-menu__main--menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  margin-top: 32px; }

.left-float-menu__main--button {
  padding-left: 24px;
  position: relative; }

.left-float-menu__main--button:hover {
  color: #7e7e82; }

.left-float-menu__main--button::after {
  position: absolute;
  top: 100%;
  left: 24px;
  width: calc(100% - 24px);
  height: 4px;
  background-color: rgba(0, 0, 0, 0.1);
  content: "";
  opacity: 0;
  transition: opacity 0.3s, transform 1s;
  transform: translateY(10px); }

.left-float-menu__main--button:hover::after {
  opacity: 1;
  transform: translateY(0); }

.left-float-menu__main--button:hover::before {
  content: '';
  height: 32px;
  width: 4px;
  border-right: 4px solid #41f6d7;
  border-radius: 4px;
  margin-left: -4px;
  position: absolute;
  left: 0;
  top: -4px; }

